import React, { Component } from "react";
import { Button, Form } from "react-bootstrap";
import Swal from "sweetalert2";

class FormularioRegistro extends Component {
  state = {
    username: "",
    password: "",
    password2: "",
    email: "",
    name: "",
    errores: {
      username: false,
      password: false,
      password2: false,
      email: false,
      name: false,
    },
  };

  error(errores, name) {
    errores[name] = true;
    this.setState({
      errores: errores,
    });
    return true;
  }

  correcto(errores, name) {
    errores[name] = false;
    this.setState({
      errores: errores,
    });
    return false;
  }

  verificar(name, value) {
    const errores = this.state.errores;

    if (value.trim() === "") {
      return this.error(errores, name);
    } else if (name === "email") {
      if (!/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value)) {
        return this.error(errores, name);
      } else {
        return this.correcto(errores, name);
      }
    } else if (name === "password2") {
      if (value !== this.state.password) {
        return this.error(errores, name);
      } else {
        return this.correcto(errores, name);
      }
    } else {
      return this.correcto(errores, name);
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();

    let error = [false, false, false, false, false];
    let errorGeneral = false;

    error[0] = this.verificar("username", this.state.username);
    error[1] = this.verificar("password", this.state.password);
    error[2] = this.verificar("email", this.state.email);
    error[3] = this.verificar("name", this.state.name);
    error[4] = this.verificar("password2", this.state.password2);

    error.forEach((element) => {
      if (element) {
        errorGeneral = true;
      }
    });

    if (!errorGeneral) {
      //login en server
      this.fetchServer();
    }
  };

  fetchServer = async () => {
    const data = {
      username: this.state.username,
      password: this.state.password,
      email: this.state.email,
      name: this.state.name,
    };

    const response = await fetch("/signup", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const body = await response.json();

    if (body.code === 1) {
      //usuario existente (por nombre o email?)
      Swal.fire({
        title: "Error",
        text: "El nombre de usuario ya está registrado",
        icon: "error",
        confirmButtonText: "Ok",
      });
    } else if (body.code === 2) {
      //error de servidor
      Swal.fire({
        title: "Error",
        text: "Ha ocurrido un error inesperado. Intenta nuevamente mas tarde",
        icon: "error",
        confirmButtonText: "Ok",
      });
    } else if (body.code === 3) {
      //error de servidor
      Swal.fire({
        title: "Error",
        text: "El email ya esta registrado",
        icon: "error",
        confirmButtonText: "Ok",
      });
    } else {
      Swal.fire({
        title: "Registro exitoso",
        text: "Puedes iniciar sesión",
        timer: 2000,
        showCancelButton: false,
        showConfirmButton: false,
      }).then(() => {
        window.location.href = "/login";
      });
    }
  };

  render() {
    return (
      <Form onSubmit={(e) => this.handleSubmit(e)}>
        <div>
          <Form.Group>
            <Form.Label className="mb-1">Nombre completo</Form.Label>
            <Form.Control
              type="text"
              name="name"
              placeholder="Ingrese su nombre"
              isInvalid={this.state.errores.name}
              value={this.state.name}
              onChange={(e) => this.setState({ name: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un nombre valido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label className="mb-1">E-mail</Form.Label>
            <Form.Control
              type="email"
              name="email"
              placeholder="Ingrese su email"
              isInvalid={this.state.errores.email}
              value={this.state.email}
              onChange={(e) => this.setState({ email: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un email valido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label className="mb-1">Usuario</Form.Label>
            <Form.Control
              type="text"
              name="username"
              placeholder="Ingrese su nombre de usuario"
              isInvalid={this.state.errores.username}
              value={this.state.username}
              onChange={(e) => this.setState({ username: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un parametro valido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label className="mb-1">Contraseña</Form.Label>
            <Form.Control
              type="password"
              name="password"
              placeholder="Ingrese su contraseña"
              isInvalid={this.state.errores.password}
              value={this.state.password}
              onChange={(e) => this.setState({ password: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un parametro valido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label className="mb-1">Repetir contraseña</Form.Label>
            <Form.Control
              type="password"
              name="password2"
              placeholder="Ingrese su contraseña"
              isInvalid={this.state.errores.password2}
              value={this.state.password2}
              onChange={(e) => this.setState({ password2: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Las contraseñas no coinciden
            </Form.Control.Feedback>
          </Form.Group>
        </div>
        <div className="text-end mt-4">
          <Button variant="outline-primary" type="submit" className="w-100">
            Enviar
          </Button>
        </div>
      </Form>
    );
  }
}

export default FormularioRegistro;
