//import "bootstrap/dist/css/bootstrap.min.css";
import "bootswatch/dist/morph/bootstrap.min.css"
import "./App.css";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import AcercaDe from "./pages/AcercaDe";
import Citas from "./pages/Citas";
import Inicio from "./pages/Inicio";
import Login from "./pages/Login";
import Registro from "./pages/Registro";
import Perfil from "./pages/Perfil";
import Pag404 from "./pages/Pag404";
import React from "react";

function App() {
  const [isAuthenticated, setIsAuthenticated] = React.useState(false);
  const [user, setUser] = React.useState({});

  return (
    <div className="App">
      <Router>
        <Routes>
          <Route exact path="/" element={<Inicio isAuthenticated={isAuthenticated} user={user} setUser={setUser} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="/citas" element={<Citas isAuthenticated={isAuthenticated} user={user} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="/acercaDe" element={<AcercaDe isAuthenticated={isAuthenticated} user={user} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="/login" element={<Login isAuthenticated={isAuthenticated} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="/registro" element={<Registro isAuthenticated={isAuthenticated} user={user} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="/profile" element={<Perfil isAuthenticated={isAuthenticated} user={user} setIsAuthenticated={setIsAuthenticated}/>} />
          <Route path="*" element={<Pag404 isAuthenticated={isAuthenticated} user={user} setIsAuthenticated={setIsAuthenticated}/>} />
        </Routes>
      </Router>
      <link
        rel="stylesheet"
        href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css"
        integrity="sha384-1BmE4kWBq78iYhFldvKuhfTAU6auU8tT94WrHftjDbrCEXSU1oBoqyl2QvZ6jIW3"
        crossOrigin="anonymous"
      />
    </div>
  );
}

export default App;
