import React from 'react';
import { Container,Row } from 'react-bootstrap';
import Contacto from './Contacto';

const Footer = () => {
    return (
        <div className='bg-dark py-5 footer'>
            <Container>
                <Row className='footer-query'>
                    <div className='col-sm-12 col-md-3 tituloFooter'>
                        <img src='/img/logo-text.png' className='w-75' alt='logo'></img>
                        <p className='text-light lead'>La obra social con los mejores DOGtores</p>
                    </div>
                    <div className='col-sm-12 col-md-9'>
                        <Contacto/>
                    </div>
                </Row>
            </Container>
        </div>
    );
};

export default Footer;