import React from "react";
import FormularioContacto from "./formularios/FormularioContacto";

const Contacto = () => {
  return (
    <div className="contacto text-light">
      <h3 className="display-6 text-light">Contacto</h3>
      <div className="row reverse-query">
        <div className="col-sm-12 col-md-5 mapa">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3283.7971909705875!2d-58.36843578451707!3d-34.60928958045801!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95a3352bde23077d%3A0xd3b460881855c395!2sPuerto%20Madero%2C%20CABA!5e0!3m2!1ses!2sar!4v1641152322840!5m2!1ses!2sar"
            style={{ height: "100%", width: "100%" }}
            loading="lazy"
            title="mapa"
          ></iframe>
        </div>
        <div className="col-sm-12 col-md-7">
          <FormularioContacto />
        </div>
      </div>
    </div>
  );
};

export default Contacto;
