import React, { Component } from "react";
import { Alert, Button, Form } from "react-bootstrap";
import { Link, Navigate } from "react-router-dom";

class FormularioLogin extends Component {
  state = {
    username: "",
    password: "",
    errores: { username: false, password: false },
    redirect: false,
  };

  verificar(name, value) {
    const errores = this.state.errores;

    if (value.trim() === "") {
      errores[name] = true;
      this.setState({
        errores: errores,
      });
      return true;
    } else {
      errores[name] = false;
      this.setState({
        errores: errores,
      });

      return false;
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();

    let error = [false, false];
    let errorGeneral = false;

    error[0] = this.verificar("username", this.state.username);
    error[1] = this.verificar("password", this.state.password);

    error.forEach((element) => {
      if (element) {
        errorGeneral = true;
      }
    });

    if (!errorGeneral) {
      this.fetchServer();
    }
  };

  fetchServer = async () => {
    const data = {
      username: this.state.username,
      password: this.state.password,
    };

    const response = await fetch("/signin", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
      credentials: "include",
    });
    const body = await response.json();

    console.log(body);

    if (body.code === 0) {
      this.setState({
        redirect: true,
      });
    } else {
      document.getElementById("alertLogin").style.display = "block";
    }
  };

  render() {
    if (this.state.redirect) return <Navigate to="/" />;
    return (
      <Form onSubmit={(e) => this.handleSubmit(e)}>
        <Alert variant="danger" id="alertLogin" style={{ display: "none" }}>
          <i className="fas fa-times-circle me-3"></i>Datos incorrectos
        </Alert>
        <div className="formLogin">
          <Form.Group>
            <Form.Label className="mb-1">Usuario</Form.Label>
            <Form.Control
              type="text"
              name="username"
              placeholder="Ingrese su usuario"
              isInvalid={this.state.errores.username}
              value={this.state.username}
              onChange={(e) => this.setState({ username: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un parametro valido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mt-3">
            <Form.Label className="mb-1">Contraseña</Form.Label>
            <Form.Control
              type="password"
              name="password"
              placeholder="Ingrese su contraseña"
              isInvalid={this.state.errores.password}
              value={this.state.password}
              onChange={(e) => this.setState({ password: e.target.value })}
              onBlur={(e) => this.verificar(e.target.name, e.target.value)}
            />
            <Form.Control.Feedback type="invalid">
              Ingrese un parametro valido
            </Form.Control.Feedback>
          </Form.Group>
          <p className="text-secondary mt-2">
            ¿No está registrado?{" "}
            <Link to="/registro" className="text-secondary">
              Registrese
            </Link>
          </p>
        </div>
        <div className="text-end mt-3">
          <Button variant="outline-primary" type="submit" className="mb-0">
            Enviar
          </Button>
        </div>
      </Form>
    );
  }
}

export default FormularioLogin;
