import React from 'react';
import Titulos from '../components/Titulos';
import FormularioRegistro from '../components/formularios/FormularioRegistro';
import Header from '../components/Header';

const Registro = (props) => {
  const fetchData = async () => {
    const res = await fetch("/auth", {
      method: "GET",
      credentials: "include",
    });
    const data = await res.json();

    if(data.code === 0){
      props.setIsAuthenticated(true);
    }
  }

  fetchData();

  if (props.isAuthenticated) {
    window.location.href = "/";
  }

  return (
    <>
      <Header isAuthenticated={props.isAuthenticated} setIsAuthenticated={props.setIsAuthenticated}/>
      <main>
        <div>
          <img src="/img/carousel/2.jpg" alt='staff de veterinaria' className='imgHeaderRegistro'></img>
        </div>
        <div className='container my-3'>
          <Titulos titulo="Registro"/>
          <div className='row'>
            <div className='col-sm-12 col-md-6'> 
              <FormularioRegistro />
            </div>
            <div className='col-sm-12 col-md-6'>
              <img src="/img/varias/perro.png" alt='perro en consultorio' className='imgRegistro'></img>
            </div>
          </div>
        </div>
      </main>
    </>
  );
};

export default Registro;