import React from "react";
import RecuadroLogin from "../components/login/RecuadroLogin";
import Header from "../components/Header";

const Login = (props) => {
  const fetchData = async () => {
    const res = await fetch("/auth", {
      method: "GET",
      credentials: "include",
    });
    const data = await res.json();

    //!!!!CAMBIAR TEXT

    console.log(data);

    if(data.code === 0){
      props.setIsAuthenticated(true);
    }
  }

  fetchData();

  if (props.isAuthenticated) {
    window.location.href = "/";
  }

  return (
    <>
      <Header isAuthenticated={props.isAuthenticated} setIsAuthenticated={props.setIsAuthenticated}/>
      <main className="bg-dark mainLogin">
        <div className="divImgLogin">
          <img
            src="/img/login/bg-big.jpg"
            alt="zoo health"
            className="imgLogin"
          />
          <RecuadroLogin/>
        </div>
      </main>
    </>
  );
};

export default Login;
