import React from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";

const Header = (props) => {

  const logout = async () => {
    await fetch("/logout", {
      method: "DELETE",
      credentials: "include",
    });
    props.setIsAuthenticated(false);
    sessionStorage.removeItem("isFirstTime");

    Swal.fire({
      title: "Sesión cerrada",
      text: "Hasta pronto",
      icon: "success",
      timer: 2000,
      showCancelButton: false,
      showConfirmButton: false
    }).then(()=>{
      window.location.href = "/";
    });
  }

  if(props.isAuthenticated) {
    return(
    <div className="header">
    <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand>
          <Link to="/">
            <img
              src="/img/logo.png"
              style={{ height: "60px" }}
              alt="logo"
            ></img>
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto linksHeader">
            <Link className="nav-link" to="/">
              Inicio
            </Link>
            <Link className="nav-link" to="/citas">
              Citas
            </Link>
            <Link className="nav-link" to="/acercaDe">
              Acerca de
            </Link>
            <Link className="nav-link" to="/profile">
              Mi perfil
            </Link>
            <button className="btn btn-danger my-2" onClick={()=>logout()}>Log out</button>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  </div>)
  }

  return (
    <div className="header">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand>
            <Link to="/">
              <img
                src="/img/logo.png"
                style={{ height: "60px" }}
                alt="logo"
              ></img>
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto linksHeader">
              <Link className="nav-link" to="/">
                Inicio
              </Link>
              <Link className="nav-link" to="/citas">
                Citas
              </Link>
              <Link className="nav-link" to="/acercaDe">
                Acerca de
              </Link>
              <Link to="/login" className="btn btn-danger text-light my-2 px-3">
                Log in
              </Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};

export default Header;
