import React from 'react';

const Titulo = () => {
  return (
    <div>
      <div className='tituloCarousel'>
        <h1 className='display-1 text-light'>Zoo Health</h1>
        <hr className='mt-0'/>
        <p className='lead'>La obra social con los mejores DOGtores</p>
        <div className='mt-5'>
          <img src="/img/logo-min.png" alt="logo" className='logoCarousel'/>
        </div>
      </div>
      <p className='arrowCarousel'><i className="fas fa-angle-double-down"></i></p>
    </div>
  );
};

export default Titulo;