import React from "react";
import CarouselIndex from "../components/index/CarouselIndex";
import Footer from "../components/Footer";
import Header from "../components/Header";
import ToastLogin from "../components/index/ToastLogin";

const Inicio = (props) => {
  const [isFirstTime, setIsFirstTime] = React.useState(true);

  const fetchData = async () => {
    const res = await fetch("/auth", {
      method: "GET",
      credentials: "include",
    });
    const data = await res.json();

    if(data.code === 0){
      props.setIsAuthenticated(true);
      props.setUser(data.user);

      if(sessionStorage.getItem("isFirstTime") === null){
        setIsFirstTime(true);
        sessionStorage.setItem("isFirstTime", true);
      }
      else {
        setIsFirstTime(false);
        sessionStorage.setItem("isFirstTime", false);
      }
    }
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  return (
    <>
      <Header isAuthenticated={props.isAuthenticated} setIsAuthenticated={props.setIsAuthenticated}/>
      <main className="bg-dark">
        <CarouselIndex />
      </main>
      <Footer />
      { (props.isAuthenticated && isFirstTime) ? <ToastLogin user={props.user}/> : null}
    </>
  );
};

export default Inicio;
