import React from "react";
import Header from "../components/Header";
import { Link } from "react-router-dom";

const Pag404 = (props) => {
  return (
    <>
      <Header />
      <main className="d-flex flex-column justify-content-center align-items-center position-relative text-center">
        <h1 className="display-2 text-dark">Ups! Pagina no encontrada</h1>
        <div>
          <Link to="/" className="btn btn-danger mt-3">
            Volver a inicio
          </Link>
        </div>
        <img src="/img/logo-min-triste-cropped.png" alt="logo triste" className="imgLogoTriste"></img>
      </main>
    </>
  );
};

export default Pag404;
